




































































































































import {Component, Vue, Watch} from "vue-property-decorator"
import {BookingGender, IBookingAvailability, IPayment, ISubscription, TrainingSessionDuration, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import Profile from "@/managers/profile"
import RequestRadio from "@/components/RequestRadio.vue"
import CardPayment from "@/components/CardPayment.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"
import HyModal from "@/components/HyModal.vue"
import { IPaymentMethod } from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"

@Component({ components: { CardPayment, HyModal, RequestRadio } })
export default class CardPlans extends Vue {
	private modal: boolean | null = null
	private modal1: boolean | null = null
	private modalType: string | null = null
	private modalText: TranslateResult | null = null
	private modalSlot: TranslateResult | null = i18n.t("cardPlan.text1")
	private isSubscribed: ISubscription | null = null
	private subscriptionCode: string = ""
	private savedCard: IPaymentMethod[] | null = null
	private isTrial: boolean | null = null
	private price: string = ""
	private basicPrice: string = ""
	private standardPrice: string = ""
	private proPrice: string = ""
	private planData: string = ""
	private planInfo: string = ""
	private planCancel: boolean | null = null
	private bflag: boolean = true
	private bsubscribed: boolean = true
	private sflag: boolean = true
	private ssubscribed: boolean = true
	private pflag: boolean = true
	private psubscribed: boolean = true
	private heading: string = "Subscription"
	private sessionTickets: number = 0
	private routedFrom: string | null = null
	private route: string | null = null
	private countryCode: string | null = null
	private monthlyPlan: string | null = null
	private cardDesc: string | null = null
	private percentSteady: string | null = null
	private percentGuru: string | null = null
	private risePlan: string | null = "Rise"
	private steadyPlan: string | null = "Steady"
	private guruPlan: string | null = "Guru"
	private riseMonthlyPrice: string | null = null
	private steadyMonthlyPrice: string | null = null
	private guruMonthlyPrice: string | null = null
	private created(): void {
		this.getSessionTickets()
		this.getSubscription()
		this.getCardData()
		this.studentInfo()
		if (window.localStorage.getItem("firstTimeUser") === "yes")
			this.isTrial = true
		}
	private closeModal(): void {
		this.modal = null
		this.modalType = null
		if (this.modal1)
			this.modal1 = null
	}
	private studentInfo(): void {
		Portal.client.call.student.me()
		.response
		.then(res => {
			this.countryCode = res.CountryCode
			switch (this.countryCode) {
			case "1":
				this.basicPrice = "49$"
				this.standardPrice = "79$"
				this.proPrice = "99$"
				this.percentSteady = "19"
				this.percentGuru = "33"
				this.riseMonthlyPrice = "24.50$"
				this.steadyMonthlyPrice = "19.75$"
				this.guruMonthlyPrice = "16.50$"
				break
			case "44":
				this.basicPrice = "39£"
				this.standardPrice = "69£"
				this.proPrice = "89£"
				this.percentSteady = "12"
				this.percentGuru = "24"
				this.riseMonthlyPrice = "19.50£"
				this.steadyMonthlyPrice = "17.25£"
				this.guruMonthlyPrice = "14.83£"
				break
			case "45":
				this.basicPrice = "295Kr"
				this.standardPrice = "495Kr"
				this.proPrice = "595Kr"
				this.percentSteady = "16"
				this.percentGuru = "33"
				this.riseMonthlyPrice = "147.50Kr"
				this.steadyMonthlyPrice = "123.75Kr"
				this.guruMonthlyPrice = "99.17Kr"
				break
			case "46":
				this.basicPrice = "395SEK"
				this.standardPrice = "595SEK"
				this.proPrice = "695SEK"
				this.percentSteady = "25"
				this.percentGuru = "41"
				this.riseMonthlyPrice = "197.50sek"
				this.steadyMonthlyPrice = "148.75sek"
				this.guruMonthlyPrice = "115.83sek"
				break
			case "47":
				this.basicPrice = "395NOK"
				this.standardPrice = "595NOK"
				this.proPrice = "695NOK"
				this.percentSteady = "25"
				this.percentGuru = "41"
				this.riseMonthlyPrice = "197.50nok"
				this.steadyMonthlyPrice = "148.75nok"
				this.guruMonthlyPrice = "115.83nok"
				break
			default:
				this.basicPrice = "49$"
				this.standardPrice = "79$"
				this.proPrice = "99$"
				this.percentSteady = "19"
				this.percentGuru = "33"
				this.riseMonthlyPrice = "24.50$"
				this.steadyMonthlyPrice = "19.75$"
				this.guruMonthlyPrice = "16.50$"
		}
		})
	}
	private checkRoute(): void {
		this.routedFrom = window.localStorage.getItem("route")
		this.route = window.localStorage.getItem("routeplaninfo")
		window.localStorage.removeItem("routeplaninfo")
		window.localStorage.removeItem("route")
		if (this.routedFrom === "cardadd")
			this.$router.push("/home")
		else if (this.route === "yes")
			this.$router.push("/home")
		else
		this.$emit("close")
		}
	private close(): void {
		this.$router.push("signin")
	}
		private getSessionTickets(): void {
		Portal.client.call.sessionTickets.get()
		.response
		.then(res => {
			this.sessionTickets = res.length
		})
		.catch(reason => {})
	}

	private noCard(value: string): void {
		if (this.countryCode === "44")
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "39£"
			this.planData = "subscription-a-uk"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "69£"
			this.planData = "subscription-b-uk"
			} else {
			this.planInfo = "Guru"
			this.price = "89£"
			this.planData = "subscription-c-uk"
			}
		else if (this.countryCode === "45")
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "295Kr"
			this.planData = "subscription-a"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "495Kr"
			this.planData = "subscription-b"
			} else {
			this.planInfo = "Guru"
			this.price = "595Kr"
			this.planData = "subscription-c"
			}
		else if (this.countryCode === "1")
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "49$"
			this.planData = "subscription-a-us"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "79$"
			this.planData = "subscription-b-us"
			} else {
			this.planInfo = "Guru"
			this.price = "99$"
			this.planData = "subscription-c-us"
			}
		else if (this.countryCode === "46")
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "395SEK"
			this.planData = "subscription-a-sek"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "595SEK"
			this.planData = "subscription-b-sek"
			} else {
			this.planInfo = "Guru"
			this.price = "695SEK"
			this.planData = "subscription-c-sek"
			}
		else if (this.countryCode === "47")
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "395NOK"
			this.planData = "subscription-a-nor"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "595NOK"
			this.planData = "subscription-b-nor"
			} else {
			this.planInfo = "Guru"
			this.price = "695NOK"
			this.planData = "subscription-c-nor"
			}
		else
			if (value === "card-a") {
			this.planInfo = "Rise"
			this.price = "49$"
			this.planData = "subscription-a-us"
			} else if (value === "card-b") {
			this.planInfo = "Steady"
			this.price = "79$"
			this.planData = "subscription-b-us"
			} else {
			this.planInfo = "Guru"
			this.price = "99$"
			this.planData = "subscription-c-us"
			}
		if (this.savedCard!.length === 0)
			this.modal = true
		else
			this.modal1 = true
			}
		private getSubscription(): void {
			Portal.client.call.subscriptions.get()
				.response
				.then(res => {
					this.isSubscribed = res
					this.subscriptionCode = res.SubscriptionCode
					this.isTrial = res.IsTrial
				})
				.catch(reason => {})
		}
		private mounted(): void {
			if (window.localStorage.getItem("Login") === "Logout")
				this.$router.push("/signin")
			}
		private startPlan(): void {
			// Write here!
			Portal.client.call.subscriptions.post(this.planData)
				.response
				.then(result => {
				window.localStorage.setItem("increaseticket", "planvise")
				this.$emit("close")
				this.isSubscribed = result
				this.subscriptionCode = result.SubscriptionCode
				if (this.$route.fullPath.split("/")[1] === "plans") {
					this.$router.push("/home")
					window.localStorage.removeItem("route") }
				if (window.localStorage.getItem("firstTimeUser") === "yes") {
					window.localStorage.setItem("firstTimeUser", "No")
					this.isTrial = false }
					})
				.catch(reason => {})
			this.modal1 = null
			if (this.bsubscribed || this.ssubscribed || this.psubscribed)
				Portal.client.call.subscriptions.cancel()
				.response
				.then((res: any) => {
				this.isSubscribed = null
				this.subscriptionCode = ""
				this.bflag = true
				this.sflag = true
				this.pflag = true
				Portal.client.call.subscriptions.post(this.planData)
				.response
				.then(result => {
				this.$emit("close")
				this.isSubscribed = result
				this.subscriptionCode = result.SubscriptionCode
				})
				.catch(reason => {})
				this.modal1 = null
			})
				.catch(reason => {})
	}
		private cancelPlan(): void {
			Portal.client.call.subscriptions.cancel()
			.response
			.then(res => {
				this.planCancel = true
				this.isSubscribed = null
				this.subscriptionCode = ""
			})
			.catch(reason => {})
		}
		private switchPlan(value: string) {
			window.localStorage.setItem("increaseticket", "planvise")
			this.heading = "Switch Plan"
			if (this.subscriptionCode === "subscription-a" || this.subscriptionCode === "subscription-a-us"
			|| this.subscriptionCode === "subscription-a-uk" || this.subscriptionCode === "subscription-a-sek" || this.subscriptionCode === "subscription-a-nor") {
					this.bflag = false
					this.sflag = true
					this.pflag = true
					this.bsubscribed = true } else if	(this.subscriptionCode === "subscription-b" || this.subscriptionCode === "subscription-b-us"
					|| this.subscriptionCode === "subscription-b-uk" || this.subscriptionCode === "subscription-b-sek" || this.subscriptionCode === "subscription-b-nor") {
					this.sflag = false
					this.bflag = true
					this.pflag = true
					this.ssubscribed = true} else {
					this.pflag = false
					this.sflag = true
					this.bflag = true
					this.psubscribed = true }
			this.isSubscribed = null
			this.subscriptionCode = ""
			}
		private getCardData(): void {
			Portal.client.call.payments.paymentMethodsGet("card")
				.response
				.then(res => {
					this.savedCard = [].slice.call(res)
				})
				.catch(reason => {})
		}
}
